@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.students-list{
  width: 400px;
  height: 700px;
  background-color: #fff;
  border-radius: 5px;
  /* box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25); */
}
.all-students{
  margin-top: 10px;
  width: 390px;
  margin-left:5px;
  overflow-x: hidden;
  overflow-y: scroll;
  height: 640px;
}
.title{
  font-size: 20px;
  margin-top: 20px;
  margin-left: 20px;
  color: #d1d1d1
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}
::-webkit-scrollbar-thumb {
  background: rgb(180, 180, 180);
  border-radius: 4px;
}
.view{
  width: 1000px;
  height: 700px;
  border-radius: 5px;
}
.view-content{
  margin-top: 10px;
  width: 920px;
  height: 550px;
  border-radius: 5px;
  margin-left: 40px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.card{
  width: 340px;
  height: 40px;
  margin-left: 20px;
  margin-top: 5px;
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 10px;
  color: rgba(0, 0, 0, .5);
  cursor: pointer;
}
.card:hover{
  color: rgba(0,0,0, .8)
}
.card:active{
  height: 40px;
  color: rgba(0, 0, 0, 1);
  font-weight: normal;
}
.card > p{
  width: 290px;
  height: 20px;
  white-space: nowrap; 
  overflow: hidden;
  text-overflow: ellipsis;
}
.person{
  font-size: 16px;
  color: #222831;
  font-weight: 600;
  opacity: .5;
}
.sub-card{
  width: 100%;
  height: auto;
  margin-left: 20px;
}
.card-img{
  margin-right: 10px;
  opacity: .4
}

.card:hover .card-img{
  opacity: .8;
}
.card:active .card-img{
  opacity: 1;
}
.card:hover .person{
  opacity: .8;
}
.card:active .person{
  opacity: 1;
}

.view-header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    height: 40px;
    border-bottom: 2px solid #e6e4e4;
  }
  .view-img{
    margin-right: 10px;
    margin-left: 10px;
  }
  .view-header>img, .view-header>p{
      opacity: .4
  }
.search{
    width: 920px;
    height: 40px;
    background: #fff;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    margin-top: 20px;
    margin-left: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    transition: .2s;
  }
  .search-input{
    border-radius: 5px;
    height: 32px;
    margin-left: 10px;
    padding-left: 10px;
    width: 770px;
    background-color: #f2f2f2;
    margin-right: 20px;
  }
  .search-item{
    transition: .5s;
    width: 32px;
    height: 32px;
    padding-left: 10px;
    padding-right: 10px;
    border-left: 2px solid rgba(0, 0, 0, 0.1);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .search-img{
    opacity: .2;
    transition-duration: .5s;
  }
  
  .text-item{
    margin-left: 10px;
    white-space: nowrap; 
    text-overflow: ellipsis;
    overflow: hidden;
    opacity: .4;
    display: none;
    width: 250px;
  }
  .search-item:hover .text-item{
    opacity: .8;
    display: block;
  }
  .search-item:hover .search-img{
    opacity: .8;
  }
  .search-item:hover{
    width: 200px;
  }

.commitTemplate{
    position: absolute;
    z-index: 2;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
    border-radius: 10px 0px 10px 10px;
    width: 900px;
    margin-left: 40px;
    padding: 10px;
    margin-top: 10px;
    max-height: 500px;
    min-width: 50px;
    overflow-y: scroll;
    overflow-x: hidden;
    transition: .5s;
}
.commitCard{
    background-color: #f1f1f1;
    width: 890px;
    height: 40px;
    margin-bottom: 10px;
    border-radius: 4px;
    display: block;
    opacity: .5;
    transition-duration: .2s;
}
.commitMessage{
    width: 500px;
    height: 30px;
    text-overflow: ellipsis;
    overflow: hidden;
    padding-top: 10px;
    margin-left: 10px;
    white-space: nowrap;
}
.commitCard:hover{
    background-color: #eee;
    opacity: 1;
}
.commitDate{
    height: 30px;
    width: auto;
    margin-top: -30px;
    margin-left: 565px;
    float:left;
}
.Tree{
    width: 880px;
    height: auto;
}
.prev-Tree{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100px;
    height: 30px;
    border-radius: 4px;
    background-color: #eee;
    transition: .2s;
    opacity: .5;
}

.prev-Tree:hover{
    background-color: #e1e1e1;
    opacity: .8;
}

.prev-Tree:active{
    background-color: #e9e9e9;
}
.prev-Tree:disabled{
    background-color: white;
}
.tree-card{
    transition: .4s;
    margin-top: 20px;
    padding-left: 10px;
    width: 100%;
    height: 30px;
    opacity: .4;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.tree-card-item{
    width: 29%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-left: 10px;
}
.tree-card:hover{
    background: #f2f2f2;
    border-radius: 5px;
    padding: 10px;
    opacity: .8;
}

.tree-card-img{
    margin-right: 10px;
    opacity: .4;
    display: none;
    transition: .2s;
}
.tree-card:hover .tree-card-img{
    display: block;
    opacity: .8;
}
.tree-card-img:hover{
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  opacity: 1;
}

.ViewCode{
    background-color: #fff;
    width: 1000px;
    height: 700px;
    border-radius: 4px;
    padding: 25px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    font-size: 15px;
    overflow: scroll;
}
.closeBtn{
    width: 22px;
    height: 22px;
    position: fixed;
    margin-left: -100px;
    margin-top: -25px;
    padding: 10px;
    transition: .5s;
    border-radius: 5px;
    border: 2px solid #212121;
    opacity: .2;
}
.closeBtn:hover{
    padding: 10px;
    border-radius: 50px;
    opacity: .4;
}
.closeBtn:active{
    opacity: 1;
}
.bg-view{
    position: fixed;
    z-index: 900;
    width: 100%;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgba(0, 0, 0, 0.2);   
}

.filter{
    margin-top: 20px;
    margin-left: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    grid-gap: 10px;
    gap: 10px;
}
.filter-item{
    border:2px solid #eee;
    box-shadow: 0px 0px 2px #eee;
    border-radius: 5px;
    padding: 5px;
}
*{
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
body{
  width: 100%;
  height: 100vh;
}
.main-content{
  width: 100%;
  height: 100vh;
  background-color: #fff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 50px;
  gap: 50px;
}

